<template>
  <div>
    <template>
      <v-row class="px-5">
        <v-card flat tile width="100%" class="barracolor mb-5">
          <v-card-text class="white--text subtitle-1 d-flex justify-space-between py-2">
            2. EMISIONES DE CO2
          </v-card-text>
        </v-card>
        <v-row>
            <v-col cols="12" class="caption px-5">
              La informacin disponible de esta seccin corresponde a las emisiones de CO<sub>2</sub> (asociadas al consumo de energa elctrica en las infraestructuras), expresadas en toneladas equivalentes para los meses seleccionados. En la columna Promedios globales, se encuentran los datos de las emisiones del total de infraestructuras. En la columna Promedios segn filtro, se encuentran los datos de las emisiones en funcin a los filtros seleccionados segn ciudad, dependencia y/o mes, y finalmente el % de variacin segn filtro presenta la variacin de la seleccin filtrada respecto al total. Las frmulas utilizadas se detallan en el manual de usuario.
            </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-card>
              <v-card-text class="py-2 caption black--text">
                <GraficoTabla :totales="totales" :cabecera="cabecera" />
              </v-card-text>
            </v-card>
          </v-col>
          <v-col cols="12">
            <BarMultiVertical 
              :inicial="inicialGrafico1" 
              :final="finalGrafico1" 
              titulo="Histrico de emisiones de CO<sub>2</sub>" 
              tituloGrafico="Emisiones de CO2 mensual"
              tituloX="Mes - Gestin"
              tituloY="CO2 [teq]"
            />
              <v-col cols="12" class="caption px-5">
                El grfico de barras muestra los datos de la sumatoria de las emisiones de CO<sub>2</sub> mensual del total de las infraestructuras (global), y, de la seleccin realizada.
              </v-col>
          </v-col>
          <v-col cols="12">
            <LineGraph :inicial="inicialGrafico2" 
              :final="finalGrafico2" 
              titulo="Comparacin de emisiones promedio de CO<sub>2</sub>" 
              tituloGrafico="Emisiones promedio mensual de CO2"
              tituloX="Mes - Gestin"
              tituloY="teq CO2(Promedio)" />
              <v-col cols="12" class="caption px-5">
                El grfico de lneas muestra las emisiones promedio mensual de CO<sub>2</sub> del total de las infraestructuras (global), y, de la seleccin realizada.
              </v-col>
          </v-col>
          <v-col cols="12">
            <LineGraph :inicial="inicialGrafico3" 
              :final="finalGrafico3" 
              titulo="Variacin porcentual de emisin de CO<sub>2</sub>" 
              tituloGrafico="Variacin porcentual mensual"
              tituloX="Mes - Gestin"
              tituloY="Porcentaje" />
              <v-col cols="12" class="caption px-5">
                El grfico de lneas muestra el incremento o decremento de los promedios de emisiones de CO<sub>2</sub> del total de las infraestructuras (global), y, de la seleccin realizada.
              </v-col>
          </v-col>
        </v-row>      
        <template>
            <v-row>
              <v-col cols="auto">
                <v-dialog
                  transition="dialog-bottom-transition"
                  scrollable
                  max-width="600px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      class="px-6"
                      small
                      color="primary"
                      v-bind="attrs"
                      v-on="on"
                    >Reporte</v-btn>
                  </template>
                  <template v-slot:default="dialog">
                    <v-card>
                      <v-card-text>
                        <div v-show="true" style="width:1050px" ref="documento1">
                          <ReporteIndicador2
                            :totales="totales" :cabecera="cabecera"
                            :inicialGrafico1="inicialGrafico1" 
                            :finalGrafico1="finalGrafico1" 
                            :inicialGrafico2="inicialGrafico2" 
                            :finalGrafico2="finalGrafico2" 
                            :inicialGrafico3="inicialGrafico3" 
                            :finalGrafico3="finalGrafico3" 
                            :tituloImpresion="titulo"
                          />
                        </div>
                      </v-card-text>
                      <v-card-actions class="justify-end">
                        <v-btn
                          outlined
                          color="primary"
                          @click="imprimir"
                        >
                        <v-icon left>
                          mdi-file-pdf
                        </v-icon>
                        Generar PDF</v-btn>
                        <v-btn
                          outlined
                          color="error"
                          @click="dialog.value = false"
                        >
                        <v-icon left>
                          mdi-close-circle
                        </v-icon>
                        Cerrar</v-btn>
                      </v-card-actions>
                    </v-card>
                  </template>
                </v-dialog>
              </v-col>
            </v-row>
        </template>

      </v-row>
      <br>
    </template>
    {{ actualizar }}
  </div>
</template>
<script>

import BarMultiVertical from '../components/BarMultiVertical.vue';
import LineGraph from '../components/LineGraph.vue';
import GraficoTabla from '../components/GraficoTabla.vue';
import ReporteIndicador2 from '../components/reportes/ReporteIndicador2.vue';
import html2pdf from 'html2pdf.js'
import store from "../store";
export default {
  components:{
    GraficoTabla,
    BarMultiVertical,
    LineGraph,
    ReporteIndicador2
  },
  name: "Indicador2",
  props: {
    pglobal: {
      type:Array
    },
    pseleccion: {
      type:Array
    },
  },
  data(){
    return {
      finalGrafico1:[],
      inicialGrafico1:[],
      finalGrafico2:[],
      inicialGrafico2:[],
      finalGrafico3:[],
      inicialGrafico3:[],
      totales: {},
      global: {},
      seleccion: {},
      titulo:"",
      cabecera: ["Variables","Promedios globales","Promedios segn filtro","% de variacin segn filtro"],
    }
  },
  created(){
    this.calcularGraficas();
    this.titulo = store.state.titulo;
    this.global = this.calcularTabla(this.pglobal);
    this.seleccion = this.calcularTabla(this.pseleccion);
    this.totales = this.tablaIndicadores();
  },
  methods:{
    imprimir(){
      html2pdf(this.$refs.documento1, {
        margin: 0,
        filename: 'consumoee.pdf',
        image: { type: 'jpeg', quality: 1 },
        html2canvas: { scale: 2 },
        jsPDF: { unit: 'in', format: 'letter', orientation: 'landscape' }
      });
    },
    tablaIndicadores(){
      let total = {};
      total[0] = { texto:"Emisiones de CO<sub>2</sub> [t<sub>eq</sub>]", promedio: this.global.consumo_co2, seleccion: this.seleccion.consumo_co2, porcentaje: Math.round((this.seleccion.consumo_co2/this.global.consumo_co2)*10000)/100 };
      total[1] = { texto:"Emisiones de CO<sub>2</sub> promedio por infraestructura [t<sub>eq</sub>]", promedio: this.global.avg_consumo_co2, seleccion: this.seleccion.avg_consumo_co2, porcentaje: Math.round(((this.seleccion.avg_consumo_co2/this.global.avg_consumo_co2)-1)*10000)/100 };
      return total;
    },

    calcularTabla(datos){
      return {
        consumo_co2: datos.reduce((total,value)=>total+parseFloat(value.consumo_co2),0),
        avg_consumo_co2: Math.round((datos.reduce((total,value)=>total+parseFloat(value.avg_consumo_co2),0)/datos.length)*100)/100,
      }
    },

    calcularGraficas(){
      this.inicialGrafico1 = this.pglobal.map(value=>{ 
        const container = {};
        container.serie1 = value.consumo_co2;
        container.categoria = value.abr_mes +"-"+ value.gestion;
        return container;
      });
      this.finalGrafico1 = this.pseleccion.map(value=>{ 
        const container = {};
        container.serie2 = value.consumo_co2;
        return container;
      });
      this.inicialGrafico2 = this.pglobal.map(value=>{ 
        const container = {};
        container.serie1 = parseFloat(value.avg_consumo_co2);
        container.categoria = value.abr_mes +"-"+ value.gestion;
        return container;
      });
      this.finalGrafico2 = this.pseleccion.map(value=>{ 
        const container = {};
        container.serie2 = parseFloat(value.avg_consumo_co2);
        return container;
      });
      this.inicialGrafico3=[];
      this.inicialGrafico2.forEach((element,index,array) => {
        let total;
        if(index==0)
          this.inicialGrafico3.push({serie1: 0, categoria: element.categoria});
        else{
          total = ((parseFloat(this.inicialGrafico2[index].serie1)/parseFloat(this.inicialGrafico2[index-1].serie1))-1)*100;
          this.inicialGrafico3.push({serie1: Math.round(total*100)/100, categoria: element.categoria});
        }
      });
      this.finalGrafico3 = [];
      this.finalGrafico2.forEach((element,index,array) => {
        let total;
        if(index==0)
          this.finalGrafico3.push({serie2: 0});
        else{
          total = ((parseFloat(this.finalGrafico2[index].serie2)/parseFloat(this.finalGrafico2[index-1].serie2))-1)*100;
          this.finalGrafico3.push({serie2: Math.round(total*100)/100});
        }
      });
    }
  },
  
  computed: {
    actualizar: function(){
      this.calcularGraficas();
      this.global = this.calcularTabla(this.pglobal);
      this.seleccion = this.calcularTabla(this.pseleccion);
      this.totales = this.tablaIndicadores();
    }
  }


}
</script>
